$main-background-color: #2b2d33;
$grey-color: #9a9b9e;
$light-grey-color: #dedfdf;
$light-dark-color: #25272b;
$main-font-color: #ffffff;
$main-accent-color: #71b872;
$dark-green: #233828;
$dark-color: #17191e;
$blue-color: #6fabff;
$button-background: linear-gradient(45deg, #4294ff 0%, #42c8d1 100%);
$button-background-hover: linear-gradient(45deg, #59a1ff 0%, #33f1fe 100%);
@font-face {
  font-family: "sf_pro_displaybold";
  src: url("../fonts/SFProDisplay-Bold/SFProDisplay-Bold.ttf")
      format("truetype"),
    url("../fonts/SFProDisplay-Bold/sfprodisplay-bold-webfont.woff2")
      format("woff2"),
    url("../fonts/SFProDisplay-Bold/sfprodisplay-bold-webfont.woff")
      format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "ubuntubold";
  src: url("../fonts/Ubuntu/ubuntu-bold-webfont.ttf") format("truetype"),
    url("../fonts/Ubuntu/ubuntu-bold-webfont.woff2") format("woff2"),
    url("../fonts/Ubuntu/ubuntu-bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "ubunturegular";
  src: url("../fonts/Ubuntu/ubuntu-regular-webfont.ttf") format("truetype"),
    url("../fonts/Ubuntu/ubuntu-regular-webfont.woff2") format("woff2"),
    url("../fonts/Ubuntu/ubuntu-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-size: 20px;
  line-height: 140%;
  background-color: $main-background-color;
  color: $main-font-color;
  font-family: "ubunturegular", sans-serif;
}

// common

.page {
  overflow: hidden;
}
.wrap {
  display: flex;
  flex-wrap: wrap;
  max-width: 1440px;
  margin: 0 auto 232px;
  padding: 0 80px;
}

.container {
  width: 100%;
}
.title {
  position: relative;
  font-weight: 700;
  font-size: 52px;
  line-height: 138%;
}
.title::after {
  position: absolute;
  content: "";
  display: block;
  left: 0;
  bottom: -16px;
  height: 6px;
  border-radius: 10px;
  background-color: $main-accent-color;
  width: 220px;
}

.title-mobile {
  display: none;
  font-weight: 700;
  font-size: 42px;
  line-height: 138%;
  margin-bottom: 32px;
  text-align: center;
}

.button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: left;
  font-size: 22px;
  padding: 17px 0;
  background: $button-background;
  border-radius: 10px;
  gap: 20px;
  transition: all 0.5s;
}
.button * {
  z-index: 1;
}
.button:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  border-radius: 10px;
  background: $button-background-hover;
  transition: all 0.5s;
  opacity: 0;
}
.button:hover:before {
  transition: all 0.7s;
  opacity: 1;
  transform: scale(1.05);
}

@media (max-width: 1200px) {
  .wrap {
    padding: 0 40px;
  }
}
@media (max-width: 960px) {
  .wrap {
    padding: 0 32px;
    margin-bottom: 112px;
  }
}
@media (max-width: 770px) {
  .wrap {
    padding: 0 16px;
  }
  .button {
    gap: 10px;
  }
}

// header
.page__header {
  &-wrap {
    width: 100%;
    max-width: 1920px;
    padding: 34px 32px;
  }
  &-logo a {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header-menu {
    // .header-menu__list

    &__list {
      display: flex;
      gap: 16px;
      align-items: center;
      letter-spacing: 0.5px;
    }

    // .header-menu__list-item
    &__list-item > a {
      display: block;
      padding: 11px 32px;
      border-radius: 5px;
      transition: all 0.5s;
      font-size: 17px;
    }
    &__list-item > a:hover {
      background-color: $dark-color;
    }
    &__list-item {
      // .header-menu__list-item_outlined

      &_outlined > a {
        border: 1px solid $main-accent-color;
      }
      &_outlined > a:hover {
        background-color: $main-accent-color;
      }
    }
  }
}

@media (max-width: 1200px) {
  .page__header {
    &-wrap {
      padding: 30px 10px;
    }
    .header-menu {
      &__list {
        gap: 10px;
      }
      &__list-item a {
        font-size: 15px;
        padding: 8px 20px;
      }
    }
  }
}
@media (max-width: 960px) {
  .page__header {
    &-wrap {
      padding: 30px 10px;
    }
    .header-menu {
      &__list {
        gap: 6px;
      }
      &__list-item a {
        font-size: 14px;
        padding: 8px 10px;
      }
    }
  }
}
@media (max-width: 770px) {
  .page__header {
    &-wrap {
      padding-top: 16px;
      padding-bottom: 32px;
    }
    &-logo {
      width: 100%;
      display: flex;
      justify-content: center;
    }
    .header-menu {
      display: none;
    }
  }
}

// presentation
.page-presentation {
  &__container {
    width: 100%;
    padding: 103px 0 0;
  }
  &__header {
    display: flex;
    width: 100%;
    padding-bottom: 450px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 380px;
    padding-top: 70px;
  }

  &__title {
    font-weight: 700;
    font-size: 52px;
    line-height: 68px;
    margin-bottom: 24px;
    max-width: 400px; //чтобы слова переносились как в макете
  }

  &__subtitle {
    font-size: 22px;
    line-height: 209%;
    word-spacing: 3px;
    margin-bottom: 64px;
  }
  &__subtitle-number {
    font-weight: 700;
    padding: 4px 14px;
    background: $dark-color;
    border-radius: 10px;
  }
  &__button {
    flex: 0 0 auto;
    padding: 17px 64px;
  }

  // .page-presentation__view

  &__view {
    flex-grow: 1;
    display: flex;
    justify-content: right;
    padding-right: 13%;
  }

  // .page-presentation__view-item

  &__view-item {
    position: relative;
    width: 316px;
    height: 645px;
    background-image: url("../img/presentation/header/iPhone14.png");
    background-size: 100%;
    background-repeat: no-repeat;
  }

  // .page-presentation__view-back

  &__view-back {
    position: absolute;
    width: 1102px;
    height: 1102px;
    background-image: url("../img/presentation/header/background.png");
    background-repeat: no-repeat;
    z-index: -2;
    top: calc(50% - (1102px / 2));
    left: calc(50% - (1102px / 2));
  }
  &__footer {
    display: flex;
    gap: 32px;
  }
  &__tg-only {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__tg-benefits {
    padding-top: 54px;
  }
}

.tg-benefits__item {
  display: flex;
  gap: 32px;
  line-height: 28px;
  margin-bottom: 48px;
}
.tg-benefits__item:hover > .tg-benefits__icon {
  transform: scale(1.1);
}
.tg-benefits__icon {
  border-radius: 10px;
  width: 80px;
  height: 80px;
}
.tg-benefits__title {
  font-weight: 700;
  margin-bottom: 16px;
}

.tg-only__title {
  font-size: 32px;
  margin-top: -92px;
  margin-bottom: 38px;
  max-width: 400px;
}
.tg-only__title::after {
  width: 54%;
  left: 22%;
}

@media (max-width: 1440px) {
  .page-presentation {
    &__container {
      padding-top: 17px;
    }
    &__header {
      padding-bottom: 190px;
    }
    &__info {
      padding-top: 15px;
    }
    &__view {
      padding-right: 12%;
    }
    &__footer {
      gap: 10px;
    }
  }
}

@media (max-width: 1200px) {
  .page-presentation {
    &__header {
    }
    &__info {
      max-width: none;
      width: 50%;
    }
    &__title {
      font-size: 42px;
    }

    &__view {
      padding-right: 5%;
      width: 50%;
    }
    &__footer {
      gap: 0;
      flex-wrap: wrap;
    }
    &__tg-only {
      width: 100%;
    }
    &__tg-benefits {
      order: 1;
      width: 47.75%;
      padding: 48px 0 48px;
    }
    &__tg-benefits-left {
      margin-right: 4.5%;
    }
  }

  // tg-benefits
  .tg-benefits__item {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .tg-benefits__text {
    max-width: 300px;
  }
}

@media (max-width: 960px) {
  .page-presentation {
    &__view {
      padding-right: 0;
    }
  }
}
@media (max-width: 770px) {
  .page-presentation {
    &__container {
      padding: 0;
    }
    &__header {
      flex-direction: column;
    }
    &__view {
      justify-content: center;
      order: 0;
      width: 100%;
      padding-bottom: 32px;
    }
    &__info {
      order: 1;
      width: 100%;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
    }
    &__title {
      display: none;
    }
    &__subtitle {
      text-align: center;
      margin-bottom: 32px;
    }
    &__title-mobile {
      display: block;
    }
    &__footer {
    }
  }
}
@media (max-width: 700px) {
  .page-presentation {
    &__container {
      padding-bottom: 64px;
    }
    &__footer {
    }
    &__tg-only {
      margin-bottom: 56px;
    }
    &__tg-benefits {
      width: 100%;
      padding: 0;
    }
    &__tg-benefits-left {
      margin-right: 0;
    }
  }
  .tg-benefits__item {
  }
  .tg-benefits__text {
    max-width: 400px;
  }
}
@media (max-width: 480px) {
  .page-presentation {
    &__title-mobile {
      font-size: 36px;
    }
    &__tg-only-img {
      width: 100%;
      height: auto;
    }
    &__view-item {
      width: 291px;
      height: 593px;
    }
  }
}
@media (max-width: 380px) {
  .page-presentation {
    &__view-item {
      width: 261px;
      height: 530px;
    }
  }
}

// about

.page-about {
  // .page-about__container

  &__container {
    width: 100%;
  }

  // .page-about__header

  &__header {
    display: flex;
    gap: 32px;
    margin-bottom: 48px;
    align-items: stretch;
  }

  // .page-about__video

  &__video {
    overflow: hidden;
    position: relative;
    width: 66%;
    // height: 474px;
    padding-bottom: 37%;
    border-radius: 10px;
  }
  &__video iframe {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    left: 0;
    top: 0;
  }

  // .page-about__header-info

  &__header-info {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    padding-top: 12px;
    width: 34%;
    flex: 1 0 auto;
  }

  // .page-about__title

  &__title {
    margin-bottom: 54px;
  }

  &__title:after {
    width: 220px;
  }

  // .page-about__item

  &__item {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    gap: 16px;
    position: relative;
    border: 6px solid $light-dark-color;
    border-radius: 10px;
    padding: 32px;
  }

  // .page-about__item-icon

  &__item-icon {
    position: absolute;
    width: 80px;
    height: 80px;
    background-color: $light-dark-color;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    right: -22px;
    top: -22px;
  }

  // .page-about__item-title

  &__item-title {
    font-weight: 700;
  }

  // .page-about__item-text

  &__item-text {
  }

  // .page-about__item-note

  &__item-note {
    background: $light-dark-color;
    border-radius: 10px;
    padding: 16px;
  }

  &__bottom {
    display: grid;
    gap: 20px;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
  }
  &__bottom .page-about__item {
  }
  &__team {
    grid-column: 2/4;
    grid-row: 1/3;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 16px;
    border: 6px solid $light-dark-color;
    border-radius: 10px;
    padding: 32px;
  }
  &__team-info {
    grid-column: 1/3;
  }
  &__team-title {
    font-weight: 700;
    margin-bottom: 16px;
  }
  &__team-member {
  }
  &__team-member_other {
    grid-column: 4/6;
    padding: 18px 16px 16px;
    border-radius: 10px;
    background: $light-dark-color;
  }
}

.member {
  padding: 18px 16px 16px;
  border-radius: 10px;
  background: $light-dark-color;
  &__photo {
    display: flex;
    align-items: center;
    justify-content: left;
    margin-bottom: 16px;
  }
  &__photo img {
    border-radius: 50%;
  }
  &__position {
    font-size: 15px;
    line-height: 147%;
    color: $grey-color;
  }

  &__other-photo {
    position: relative;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    overflow: hidden;
  }
  &__other-mask {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: linear-gradient(270deg, #25272b 0%, rgba(37, 39, 43, 0) 100%);
  }
  &__other-photo img {
    border-radius: 50%;
    border: 5px solid $light-dark-color;
  }
  &__other-photo img + img {
    margin-left: -23px;
  }
}

.cost {
  padding: 9px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__title {
    flex: 1 0 auto;
    font-weight: 700;
    font-size: 32px;
    line-height: 138%;
    text-align: center;
    margin-bottom: 32px;
  }
  &__button {
    font-size: 22px;
    line-height: 164%;
    padding: 17px 71px;
    justify-content: center;

    max-width: 420px;
  }
}

@media (max-width: 1440px) {
  .page-about {
    // .page-about__container

    &__container {
    }

    // .page-about__header

    &__header {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 32px;
      margin-bottom: 0;
    }

    // .page-about__video

    &__video {
      width: 100%;
      padding-bottom: 54%;
    }

    // .page-about__header-info

    &__header-info {
      padding-top: 0;
      width: 100%;
      margin-bottom: 32px;
    }

    &__title {
      display: none;
    }
    &__title:after {
      width: 220px;
      left: calc(50% - (220px / 2));
    }
    &__title-mobile {
      display: block;
      margin-bottom: 16px;
    }

    //
    &__bottom {
      display: flex;
      flex-direction: column;
      gap: 32px;
    }
  }
}

@media (max-width: 960px) {
  .page-about {
    &__team {
      grid-template-rows: 100px 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr;
    }
    &__team-info {
      grid-column: 1/4;
      grid-row: 1/2;
    }

    &__team-member_other {
      grid-column: 1/4;
    }
    .member__other-photo {
      justify-content: left;
    }
  }
}
@media (max-width: 770px) {
  .page-about {
    &__team {
      grid-template-rows: 128px 1fr 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr;
    }
    &__team-info {
      grid-column: 1/3;
      grid-row: 1/2;
    }
    &__team-member {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__team-member_other {
      grid-column: 1/3;
    }
  }
}
@media (max-width: 420px) {
  .page-about {
    &__team {
      display: flex;
      flex-direction: column;
      gap: 16px;
    }
  }
}

