.page-price {
  position: relative;

  margin-bottom: 230px;

  // .page-price__wrap
  &__background {
    position: absolute;
    background: $light-dark-color;
    left: 0;
    right: 0;
    top: 85px;
    bottom: 85px;
    z-index: -1;
  }
  &__wrap {
  }

  // .page-price__container

  &__container {
    width: 100%;
    min-height: 676px;
    height: auto;
    padding: 64px 5% 64px 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
  }

  // .page-price__info

