  &__button {
    margin-bottom: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 17px 71px;
    flex: 0 0 auto;
  }

  // .page-price__view

  &__view {
    width: 479px;
    height: 731px;
    position: relative;
  }
  &__view-container {
    position: absolute;
    padding: 16px;
