// plan

.page-plan {
  // .page-plan__wrap

  &__wrap {
  }

  // .page-plan__container

  &__container {
    overflow: hidden;
  }
  &__grid {
    display: grid;
    width: 1280px;
    column-gap: 32px;
    row-gap: 190px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    margin-bottom: 60px;
  }

  // .page-plan__title

  &__title-block {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__title {
  }
  &__title:after {
  }
  &__title-mobile {
    margin-bottom: 50px;
  }
  &__title-mobile:after {
    width: 220px;
    left: calc(50% - 220px / 2);
  }

  // .page-plan__stages

  // &__stages {
  // }

  // .page-plan__block

  &__block {
    display: flex;
    gap: 32px;
    position: relative;
    padding: 52px 0 0 0;
  }

  // .page-plan__preparation

  &__preparation {
    grid-column: 2/4;
  }

  // .page-plan__block-title

  &__block-title {
    width: 100%;
    top: 0;
    left: 0;
    position: absolute;
    font-size: 15px;
    line-height: 147%;
    color: $grey-color;
  }
  &__block-title:after {
    content: "";
    display: block;
    width: 100%;
    height: 6px;
    background: $grey-color;
    border-radius: 10px;
    margin-top: 8px;
  }

  &__block-title_blue {
    color: $blue-color;
  }
  &__block-title_blue:after {
    background: $blue-color;
  }

  //

  ._order3 {
    order: 3;
  }
  ._order2 {
    order: 2;
  }

  //

  &__stage {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 296px;
  }

  //  оболочка внутри и снаружи блока, чтобы border-image правильно отображалась
  &__stage-info-wrap {
    border-radius: 5px;
    overflow: hidden;
  }
  &__stage-info {
    padding: 32px;
    border-radius: 10px;
    background-color: $light-dark-color;
    border: 2px solid;
    border-image-slice: 1;
    border-image-source: linear-gradient(
      180deg,
      rgba(154, 155, 158, 0.4) 0%,
      rgba(154, 155, 158, 0) 100%
    );
  }

  // .page-plan__stage-title

  &__stage-title {
    font-weight: 700;
    font-size: 32px;
    line-height: 138%;
    margin-bottom: 16px;
  }

  // .page-plan__stage-text

  &__stage-text {
    margin-bottom: 16px;
  }
  &__stage-link {
    width: 100%;
    padding: 16px;
    background-color: $main-background-color;
    display: flex;
    border-radius: 10px;
    align-items: center;
    justify-content: space-between;
  }

  // .page-plan__stage-custom

  &__stage-custom {
    position: absolute;
    width: 100%;
    height: 50px;
    left: 0;
    bottom: -50px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding-top: 16px;
  }

  // .page-plan__stage-icon

  &__stage-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    font-weight: 700;
  }
  &__stage-icon span {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    background: $light-dark-color;
    text-align: center;
    line-height: 60px;
  }
  &__stage-arrow_down {
    margin-top: -5px;
  }

  // .page-plan__stage-arrow

  &__stage-arrow {
    margin-top: 10px;
  }

  // .page-plan__payment

  &__payment {
  }

  // .page-plan__development

  &__development {
    grid-column: 2/5;
    padding: 0;
  }

  &__development &__stage-arrow {
    transform: scale(-1, 1);
  }
  &__development &__stage-info-wrap {
    height: 100%;
    order: 1;
  }
  &__development &__stage-info {
    height: 100%;
  }
  &__development &__stage-custom {
    top: -90px;
    bottom: auto;
    // padding: 0 0 16px 0;
    justify-content: flex-end;
    gap: 16px;
  }
  &__development &__stage-icon {
    order: 1;
  }

  &__development &__block-title {
    top: auto;
    bottom: -40px;
    left: 0;
    color: $main-accent-color;
    text-align: right;
  }
  &__development &__block-title:after {
    background: $main-accent-color;
    margin-top: -35px;
  }

  &__block-title_blue {
    color: $blue-color;
  }
  &__block-title_blue:after {
    background: $blue-color;
  }

  // .page-plan__guarantee

  &__guarantee {
    grid-row: 2/3;
    grid-column: 1/2;
    padding: 32px;
    border-radius: 10px;
    border: 6px solid $light-dark-color;
  }
  &__guarantee-container {
  }
}

