.answers {
  display: flex;
  gap: 32px;
  flex-direction: column;
  // .answers__item

  &__item {
    overflow: hidden;
    padding: 32px;
    background: $light-dark-color;
  }

  // .answers__item-info

  &__item-info {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 29px;
    gap: 20px;
  }

  // .answers__item-title

  &__item-title {
    font-weight: 700;
    font-size: 32px;
    line-height: 138%;

    display: inline-block;
    cursor: pointer;
  }

  // .answers__item-text

  &__item-text {
    height: 0;
    overflow: hidden;
    transition: all 0.5s;
  }
  &__item-text.active {
    opacity: 1;
    transition: all 1s;
  }

  // .answers__item-button

  &__item-button {
    min-width: 70px;
    width: 70px;
    height: 70px;
    background: $main-background-color;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.5s;
  }
  &__item-button.active {
    transform: rotate(90deg);
    transition: transform 0.5s;
  }
  &__item-button:before {
    content: "";
    display: block;
    width: 5px;
    height: 20px;
    background-color: $light-grey-color;
    border-radius: 10px;
    transform: rotate(-45deg) translate(8px, 0);
  }
  &__item-button:after {
    content: "";
    display: block;
    width: 5px;
    height: 20px;
    background-color: $light-grey-color;
    border-radius: 10px;

    transform: translate(1px, 6px) rotate(45deg);
  }
}

